import React from 'react';
import { useDrag } from 'react-dnd';
import '../../styles/ReadmeElement.css';

const ReadmeElement = ({ id, type, content, onRemove }) => {
  const [{ isDragging }, drag] = useDrag({
    type: 'README_ELEMENT',
    item: { id, type, content },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  });

  // 
  const getElementStyle = () => {
    switch (type) {
      case '':
        return 'readme-title';
      case '':
        return 'readme-paragraph';
      case '':
        return 'readme-list';
      case '':
        return 'readme-badge';
      case '':
        return 'readme-code';
      case '':
        return 'readme-content-area';
      default:
        return '';
    }
  };

  return (
    <div
      ref={drag}
      className={`readme-element ${getElementStyle()} ${isDragging ? 'dragging' : ''}`}
      style={{ opacity: isDragging ? 0.5 : 1 }}
    >
      {type === '' && <h2>{content}</h2>}
      {type === '' && <p>{content}</p>}
      {type === '' && <ul><li>{content}</li></ul>}
      {type === '' && <div className="badge-container"><img src={content} alt="" /></div>}
      {type === '' && <pre><code>{content}</code></pre>}
      {type === '' && <div className="content-area-header">{content}</div>}
      <button className="remove-button" onClick={() => onRemove(id)}></button>
    </div>
  );
};

export default ReadmeElement;