import { createContext, useContext } from 'react';
import { makeAutoObservable } from 'mobx';

//  store
class ElementStore {
  elements = [];
  
  constructor() {
    // makeAutoObservable
    makeAutoObservable(this);
  }
  
  addElement = (item, column = null, layoutId = null) => {
    // 
    if (item.type === '') {
      this.elements.push({
        id: Date.now(),
        type: item.type,
        content: item.content,
        column: null,
        layoutId: null
      });
      return;
    }

    // 
    const newElement = {
      id: Date.now(),
      type: item.type,
      content: item.content,
      column: column,
      layoutId: layoutId
    };

    // ID
    if (layoutId) {
      const layoutElement = this.elements.find(el => el.id === layoutId);
      if (layoutElement) {
        const layoutPos = this.elements.indexOf(layoutElement);
        this.elements.splice(layoutPos + 1, 0, newElement);
      }
    } else {
      // ID
      this.elements.push(newElement);
    }
  };

  removeElement = (id) => {
    this.elements = this.elements.filter(element => element.id !== id);
  };

  updateElementColumn = (id, column) => {
    const element = this.elements.find(el => el.id === id);
    if (element) {
      element.column = column;
    }
  };
}

//  store
class RootStore {
  constructor() {
    this.elementStore = new ElementStore();
  }
}

// 
export const StoreContext = createContext(null);

// useStore
export const useStore = () => {
  const store = useContext(StoreContext);
  if (!store) {
    throw new Error('useStore must be used within a StoreProvider');
  }
  return store;
};

// store - RootStore
const store = new RootStore();

export default store;