import React from 'react';
import { useDrag } from 'react-dnd';
import '../../styles/DraggableElement.css';

const DraggableElement = ({ type, content }) => {
  const [{ isDragging }, drag] = useDrag({
    type: 'README_ELEMENT',
    item: { type, content },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  });

  const renderContent = () => {
    switch (type) {
      case '':
        return <h2>{content}</h2>;
      case '':
        return <p>{content}</p>;
      case '':
        return <ul><li>{content}</li></ul>;
      case '':
        return <img src={content} alt={content} />;
      case '':
        return <pre>{content}</pre>;
      case '':
        return (
          <div className="layout-preview">
            <div className="layout-column left"></div>
            <div className="layout-column right"></div>
          </div>
        );
      case '':
        return (
          <div className="content-area-preview">
            <div className="content-area-header-preview">{content}</div>
            <div className="content-area-placeholder"></div>
          </div>
        );
      default:
        return <p>{content}</p>;
    }
  };

  return (
    <div
      ref={drag}
      className="draggable-element"
      style={{
        opacity: isDragging ? 0.5 : 1,
        cursor: 'move',
      }}
    >
      <div className="element-type">{type}</div>
      <div className="element-content">
        {renderContent()}
      </div>
    </div>
  );
};

export default DraggableElement;