// 
import React, { useState } from 'react';
import '../styles/ElementSelector.css';
import '../styles/Form.css';
import DraggableElement from './common/DraggableElement';

const ElementSelector = () => {
  const [type, setType] = useState('');
  const [content, setContent] = useState('');
  const [availableElements, setAvailableElements] = useState([
    { type: '', content: '' },
    { type: '', content: '' },
    { type: '', content: '' },
    { type: '', content: '' },
    { type: '', content: '' },
    { type: '', content: 'Markdown' },
    { type: '', content: 'https://img.shields.io/github/stars/username/repo' },
    { type: '', content: 'npm install\nnpm start' },
    // 
    // { type: '', content: '' },
    { type: '', content: '' }, // 
  ]);

  const addCustomElement = () => {
    if (!content.trim()) return;
    setAvailableElements((prev) => [
      ...prev,
      { type, content },
    ]);
    setContent('');
  };

  return (
    <div className="element-selector">
      <h3></h3>
      <div className="available-elements">
        {availableElements.map((el, index) => (
          <DraggableElement
            key={index}
            type={el.type}
            content={el.content}
          />
        ))}
      </div>
      <div className="custom-element-form">
        <h4></h4>
        <select value={type} onChange={(e) => setType(e.target.value)}>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          {/*  */}
          {/* <option value=""></option> */}
          <option value=""></option> {/*  */}
        </select>
        <input
          type="text"
          value={content}
          onChange={(e) => setContent(e.target.value)}
          placeholder="..."
        />
        <button onClick={addCustomElement}></button>
      </div>
    </div>
  );
};

export default ElementSelector;